		changeFeaStatus(){
			let _this = this
			//校验提交参数
			if(_this.saveParams.length==0){
				this.$message({
                  showClose: true,
                  message: "请选择要提交的项目",
                  type: 'warning'
                });
				return  false
			}
			console.log('saveParams',_this.saveParams)
			
			for(let i=0;i<_this.saveParams.length;i++){
				if(_this.saveParams[i].itemPhaseDet != '26'){
					this.$message({
						showClose: true,
						message: "所选项目状态必须是'可研编制已审批'",
						type: 'warning'
					});
					return false
				}
				if(_this.saveParams[i].batchId == "" || _this.saveParams[i].batchId ==null
				 || _this.saveParams[i].mgtOrg == "" || _this.saveParams[i].mgtOrg==null){
					console.log(_this.saveParams[i])
					this.$message({
						showClose: true,
						message: "所选的项目中有未挂接批次或未添加联系人的项目",
						type: 'warning'
					});
					return  false
				}
				
			}
			debugger
			console.log(_this.saveParams)
			_this.statusLoading = true
			changePlaneStatus(_this.saveParams).then(response=>{
				_this.statusLoading = false
				if(response.data.successful){
					this.$message.success("提交评审计划成功")
					_this.getTable(_this.currentPage,_this.currentNum)
					_this.saveParams = []
					_this.$refs.itemTable.clearSelection()
